img {
  max-width: 100%;
  height: auto;
}
a {
  text-decoration: none;
}
ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
@font-face {
  font-family: "Raleway";
  font-style: normal;
  font-weight: 800;
  font-display: swap;
  src: url("../../fonts/Raleway-ExtraBold.woff") format("woff");
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../../fonts/Rubik-Regular.woff") format("woff");
}
@font-face {
  font-family: "Rubik";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../../fonts/Rubik-Bold.woff") format("woff");
}
.button {
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  line-height: 21px;
  font-weight: 800;
  text-align: center;
  color: #000000;
  background-color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
}
.page {
  height: 100%;
  font-family: "Rubik", sans-serif;
  font-size: 18px;
  line-height: 30px;
  font-weight: 400;
}
.page__header {
  width: 1160px;
  margin: 0 auto;
}
.page__main {
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  min-width: 320px;
  min-height: 100%;
  background-color: #ffffff;
  color: #444444;
}
.page__footer {
  max-width: 1440px;
  margin: 0 auto;
  background-color: #444444;
}
.page__container {
  width: 1161px;
  margin: 0 auto;
}
.page__header {
  background-color: #ffe17f;
  color: #000000;
}
.navigation {
  display: flex;
  flex-direction: column;
  width: 1041px;
  margin: 0 auto;
}
.navigation__list,
.navigation__user {
  margin: 0;
  padding: 0;
}
.navigation__link {
  display: block;
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  line-height: 21px;
  color: #000000;
  font-weight: 800;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}
.navigation__user a {
  font-family: "Rubik", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
}
.title {
  margin: 0;
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 800;
  font-size: 50px;
  line-height: 50px;
  color: #000000;
}
.gallery {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.gallery__box {
  width: 560px;
}
.category__list {
  display: grid;
  grid-template-columns: repeat(6, 160px);
  column-gap: 40px;
}
.category__link {
  display: grid;
  grid-template-rows: 160px min-content;
  row-gap: 33px;
  margin: 0;
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  font-weight: 800;
  line-height: 24px;
  color: #000000;
}
.category__title {
  margin: 0;
  font-family: "Raleway";
  font-weight: 800;
  font-size: 18px;
  line-height: 24px;
}
.category__box-img {
  grid-row: 1;
  width: 160px;
  height: 160px;
  background-color: #ffe17f;
}
.delivery {
  display: flex;
  flex-direction: row-reverse;
}
.delivery__box {
  height: 100px;
  display: grid;
  grid-template-columns: 125px 1fr;
  background-color: #ebebeb;
}
.delivery__title {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  font-family: "Raleway", sans-serif;
  font-weight: 800;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: #000000;
}
.delivery__icon {
  background-color: #ffe17f;
}
.characteristic__indicator {
  color: #000000;
  font-size: 36px;
}
.characteristic__descr {
  margin: 0;
  font-size: 16px;
}
.about__item {
  color: #000000;
  font-weight: 700;
  line-height: 20px;
}
.about__descr {
  margin: 0;
}
.contacts__subtitle {
  margin: 0;
  font-weight: 700;
  font-size: 24px;
}
.contacts__address {
  font-style: normal;
}
.contacts__phone {
  color: #444444;
  font-style: normal;
}
.breadcrumbs {
  display: flex;
  flex-direction: row;
  color: #444444;
  font-size: 16px;
  line-height: 19px;
}
.breadcrumbs__link {
  color: inherit;
  margin-right: 36px;
}
.catalog-filter {
  width: 468px;
  display: flex;
  flex-direction: column;
}
.catalog-filter__title {
  font-weight: 700;
  line-height: 20px;
}
.control__label {
  font-size: 16px;
  line-height: 19px;
}
.sorting-container {
  background-color: #ebebeb;
  padding: 29px 0;
}
.sorting-type__label {
  font-family: 'Raleway';
  font-weight: 800;
  font-size: 16px;
  line-height: 19px;
  text-transform: uppercase;
}
.pagination {
  display: flex;
  flex-direction: row;
  background-color: #ebebeb;
}
.pagination__item:first-child {
  margin-right: auto;
}
.pagination__item:last-child {
  margin-left: auto;
}
.pagination__link {
  display: block;
  padding: 28px;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  color: #444444;
  opacity: 0.3;
  cursor: pointer;
}
.pagination__next,
.pagination__prev {
  font-family: 'Raleway';
  font-weight: 800;
  font-size: 16px;
  line-height: 19px;
  align-items: center;
  text-transform: uppercase;
  color: #000000;
  opacity: 1;
}
.product {
  padding: 69px 140px 77px 72px;
}
.product__list {
  display: grid;
  grid-template-columns: repeat(2, 360px);
  justify-content: center;
  column-gap: 40px;
  row-gap: 44px;
  padding-bottom: 44px;
}
.product__link {
  color: #000000;
}
.product__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 24px;
}
.product__price {
  line-height: 21px;
  color: #444444;
}
.product__show-more {
  display: flex;
  justify-content: center;
  margin-bottom: 51px;
  width: 100%;
  padding: 21px 0;
  font-size: 16px;
  line-height: 19px;
  border: 3px solid #ebebeb;
}
.footer {
  display: flex;
  flex-direction: column;
  background-color: #444444;
}
.footer__wrapper {
  display: flex;
  flex-direction: row;
}
.footer__adds {
  margin-right: auto;
}
.footer__contacts {
  margin-left: auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.footer__address {
  font-style: normal;
  color: #ffffff;
  font-size: 16px;
  line-height: 19px;
}
.footer__phone {
  font-style: normal;
  font-size: 16px;
  color: #ffffff;
  text-decoration: none;
}
.info {
  display: flex;
  flex-direction: column;
}
.info__link {
  font-family: "Raleway";
  color: #ffffff;
  font-style: normal;
  font-weight: 800;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.2em;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
}
.info__list {
  display: flex;
  flex-direction: row;
}
.info__item {
  margin-right: 34px;
}
.index-wrapper {
  display: grid;
  grid-template-columns: repeat(6, 160px);
  column-gap: 40px;
}
.index-wrapper .about {
  grid-column: span 3;
}
.index-wrapper .contacts {
  grid-column: span 3;
}
.newsletter {
  display: grid;
  grid-template-columns: repeat(6, 160px);
  column-gap: 40px;
}
.newsletter__descr {
  grid-column: span 3;
}
.newsletter__title {
  grid-column: span 3;
}
.newsletter__form {
  grid-column: span 6;
}
.form {
  display: grid;
  grid-template-columns: repeat(6, 160px);
  column-gap: 40px;
}
.form__email {
  grid-column: span 3;
}
.form__btn {
  grid-column: span 2;
}
.catalog-products {
  display: flex;
}
.main-inner {
  width: 1440px;
  margin: 0 auto;
  padding-top: 37px;
}
.catalog-content {
  width: 972px;
}
.social {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(40px, 1fr));
  gap: 15px;
}
